import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import useLocalStorage from "../hooks/useLocalStorage";
import { v4 as uuidv4 } from "uuid";

import "./BlogCreate.scss";

export default function BlogCreate() {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [author, setAuthor] = useState("");

  const [, setBlogs] = useLocalStorage("task.blogs", []);

  const history = useHistory();

  function createNewBlog(title, body, author) {
    let currentDate = new Date();
    return {
      title: title,
      time:
        currentDate.getDate() +
        "/" +
        (currentDate.getMonth() + 1) +
        "/" +
        currentDate.getFullYear(),
      author: author,
      body: body,
      id: uuidv4(),
    };
  }
  function sendForm(e) {
    e.preventDefault();
    const newBlog = createNewBlog(title, body, author);
    setBlogs((prevBlogs) => [...prevBlogs, newBlog]);
    setTimeout(() => {
      history.push("/blogs/" + newBlog.id);
    }, 0);
  }
  return (
    <section className="create">
      <h1 className="create__title title">Add a new blog</h1>
      <form onSubmit={sendForm} className="create__form container">
        <label className="create__label">
          Blog title:
          <br />
          <input
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            type="text"
            className="create__field"
            required
          />
        </label>
        <label className="create__label _wide">
          Blog body:
          <br />
          <textarea
            value={body}
            onChange={(e) => {
              setBody(e.target.value);
            }}
            className="create__area"
            required
          ></textarea>
        </label>
        <label className="create__label">
          Author:
          <br />
          <input
            value={author}
            onChange={(e) => setAuthor(e.target.value)}
            type="text"
            className="create__field"
            required
          />
        </label>
        <button className="btn create__btn" type="submit">
          Create
        </button>
      </form>
    </section>
  );
}
