import React from "react";
import { useParams } from "react-router";
import { useHistory } from "react-router-dom";
import useLocalStorage from "../hooks/useLocalStorage";

import "./BlogFile.scss";

export default function BlogFile() {
  const { id } = useParams();
  const [blogs, setBlogs] = useLocalStorage("task.blogs", []);
  const history = useHistory();
  const desiredBlog = blogs.find((blog) => blog.id === id);
  if (!desiredBlog) return <p>NOT FOUND</p>;

  function deleteBlog() {
    setBlogs((prevBlogs) => prevBlogs.filter((blog) => blog.id !== id));
    setTimeout(() => {
      history.push("/");
    }, 0);
  }
  return (
    <article className="blog">
      <header className="blog__header">
        <h1 className="blog__title title">{desiredBlog.title}</h1>
        <time className="blog__time">{desiredBlog.time}</time>
      </header>
      <p className="blog__text">{desiredBlog.body}</p>
      <address className="blog__author">{desiredBlog.author}</address>
      <button onClick={deleteBlog} className="btn blog__btn">
        Delete blog
      </button>
    </article>
  );
}
