import React from "react";
import { Link } from "react-router-dom";
//import useAPI from "../hooks/useAPI";
import useLocalStorage from "../hooks/useLocalStorage";

import "./BlogList.scss";

const initialBlogs = [
  {
    title: "Create a new blog",
    time: "3/14/2020",
    author: "Admin",
    body: "Hit the 'Create' link to open the blog redactor.",
    id: "1",
  },
  {
    title: "Hello world",
    time: "3/15/2020",
    author: "Admin",
    body:
      "It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like).",
    id: "2",
  },
];

export default function BlogList() {
  const [blogs] = useLocalStorage("task.blogs", initialBlogs);

  if (blogs.length === 0) {
    return (
      <div>
        No blogs yet.
        <br />
        ...But u can create new!
      </div>
    );
  }
  function BlogItem(props) {
    return (
      <li className="blogs__item">
        <Link to={"/blogs/" + props.blog.id} className="blogs__link">
          <h2 className="blogs__title">{props.blog.title}</h2>
          <time className="blogs__time">{props.blog.time}</time>
        </Link>
      </li>
    );
  }
  return (
    <article className="blogs">
      <ul className="blogs__list">
        {blogs.map((blog, index) => (
          <BlogItem blog={blog} key={index} />
        ))}
      </ul>
    </article>
  );
}
